"use client";

import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { getAssetBlob } from "@/lib/idbAssets";
import { useWiiController } from "@/hooks/useWiiController";
import { usePresentation } from "@/hooks/usePresentation";
import { useDrawing } from "@/hooks/useDrawing";
import { useAudio } from "@/hooks/useAudio";
import { useBindings } from "@/hooks/useBindings";
import { ReactionOverlay } from "@/components/presenter/ReactionOverlay";
import { WiiDebugPanel } from "@/components/presenter/WiiDebugPanel";
import { WiiDisconnectPopup } from "@/components/presenter/WiiDisconnectPopup";
import { WiiReconnectPopup } from "@/components/presenter/WiiReconnectPopup";
import { SlideDisplay } from "@/components/presenter/SlideDisplay";
import { DrawingCanvas } from "@/components/presenter/DrawingCanvas";
import { IrPointerOverlay } from "@/components/presenter/IrPointerOverlay";
import { EraserCursor } from "@/components/presenter/EraserCursor";
import { SerializedFlow } from "@/types";


export function PresenterView() {
    const router = useRouter();
    const searchParams = useSearchParams();
    const containerRef = useRef<HTMLDivElement | null>(null);
    const isMouseDrawingRef = useRef(false);
    const [isPainting, setIsPainting] = useState(false);
    const wasWiiADownRef = useRef(false);
    const lastEraserToggleTimeRef = useRef<number>(0);
    const lastIrSensToggleTimeRef = useRef<number>(0);

    const { wiiState, pressed, wiiConnected, wiiDisconnectedAt, irCursorEnabled, setIrCursorEnabled, playWiiSound } = useWiiController();

    const soundboardRef = useRef<{ q?: HTMLAudioElement; w?: HTMLAudioElement; e?: HTMLAudioElement }>({});
    const audioUnlockedRef = useRef(false);
    const pendingSoundRef = useRef<"q" | "w" | "e" | null>(null);

    const tryUnlockAudio = useCallback(async () => {
        if (audioUnlockedRef.current) return;

        const { q, w, e } = soundboardRef.current;
        const audios = [q, w, e].filter(Boolean) as HTMLAudioElement[];
        if (audios.length === 0) return;

        try {
            const a = audios[0];
            const prevMuted = a.muted;
            const prevVolume = a.volume;
            a.muted = true;
            a.volume = 0;
            await a.play();
            a.pause();
            a.currentTime = 0;
            a.muted = prevMuted;
            a.volume = prevVolume;
            audioUnlockedRef.current = true;
            console.log("[Audio] Successfully unlocked audio context");

            const pending = pendingSoundRef.current;
            pendingSoundRef.current = null;
            if (pending) {
                const next = soundboardRef.current[pending];
                if (next) {
                    next.currentTime = 0;
                    void next.play().catch((err) => {
                        console.warn("sound play failed", pending, err);
                    });
                }
            }
        } catch (err) {
            console.warn("audio unlock failed", err);
        }
    }, []);

    // PCÂÅ¥„ÅßÈü≥Â£∞„ÇíÂÜçÁîüÔºàHTMLAudioÔºâ
    const playSoundOnPC = useCallback(async (key: "q" | "w" | "e") => {
        const a = soundboardRef.current[key];
        if (!a) {
            console.warn(`[Audio] No audio element for key: ${key}`);
            return;
        }

        // „Ç™„Éº„Éá„Ç£„Ç™„Åå„Ç¢„É≥„É≠„ÉÉ„ÇØ„Åï„Çå„Å¶„ÅÑ„Å™„ÅÑÂ†¥Âêà„ÄÅ„Ç¢„É≥„É≠„ÉÉ„ÇØ„ÇíË©¶Ë°å
        if (!audioUnlockedRef.current) {
            console.log(`[Audio] Attempting to unlock audio context for ${key}`);
            pendingSoundRef.current = key;
            await tryUnlockAudio();
            // „Ç¢„É≥„É≠„ÉÉ„ÇØ„Å´Â§±Êïó„Åó„ÅüÂ†¥Âêà„ÅØ„Éö„É≥„Éá„Ç£„É≥„Ç∞„Å´‰øùÂ≠òÊ∏à„Åø
            if (!audioUnlockedRef.current) {
                console.warn("[Audio] Audio context not unlocked yet, sound will play after user interaction");
                return;
            }
        }

        console.log(`[Audio] Playing sound on PC: ${key}`);
        a.currentTime = 0;
        void a.play().catch((err) => {
            console.warn(`[Audio] Sound play failed for ${key}:`, err);
            pendingSoundRef.current = key;
        });
    }, [tryUnlockAudio]);

    // Wii„É™„É¢„Ç≥„É≥ÂÅ¥„ÅßÈü≥Â£∞„ÇíÂÜçÁîü
    const playSoundOnWii = useCallback(
        (key: "q" | "w" | "e") => {
            if (key === "q") playWiiSound("shot");
            else if (key === "e") playWiiSound("oh");
            else playWiiSound("uxo");
        },
        [playWiiSound],
    );

    // Ê±éÁî®„ÅÆÈü≥Â£∞ÂÜçÁîüÈñ¢Êï∞ÔºàoutputDevice„ÅßÂá∫ÂäõÂÖà„ÇíÊåáÂÆöÔºâ
    const playSound = useCallback(
        (key: "q" | "w" | "e", outputDevice: "pc" | "wii" = "pc") => {
            if (outputDevice === "wii") {
                playSoundOnWii(key);
            } else {
                playSoundOnPC(key);
            }
        },
        [playSoundOnPC, playSoundOnWii],
    );

    const returnTo = useMemo(() => {
        return searchParams.get("from") === "editor" ? "/editor" : "/";
    }, [searchParams]);

    const returnLabel = useMemo(() => {
        return returnTo === "/editor" ? "„Ç®„Éá„Ç£„Çø„Å´Êàª„Çã" : "„Éõ„Éº„É†„Å´Êàª„Çã";
    }, [returnTo]);

    const goBack = useCallback(() => {
        router.push(returnTo);
    }, [router, returnTo]);
    const [flow, setFlow] = useState<SerializedFlow | null>(null);
    const [error, setError] = useState<string | null>(null);
    const [currentNodeId, setCurrentNodeId] = useState<string | null>(null);
    const [startedWithWii, setStartedWithWii] = useState(false);
    const [playingSince, setPlayingSince] = useState<number>(0);
    const [showDebugPanel, setShowDebugPanel] = useState(true);
    const [showIrDebug, setShowIrDebug] = useState(true); // IR„Çª„É≥„Çµ„Éº„Éá„Éê„ÉÉ„Ç∞Ë°®Á§∫

    const pdfDocCacheRef = useRef<Map<string, Promise<any>>>(new Map());

    // ‚òÖ‰øÆÊ≠£: Â∏∏„Å´playingÁä∂ÊÖã„Å®„Åó„Å¶Êâ±„ÅÜÔºàflow/currentNodeId„Åå„ÅÇ„Çå„Å∞ÂÜçÁîü‰∏≠Ôºâ
    const isPlaying = flow != null && currentNodeId != null;

    // PCÂÅ¥Èü≥Â£∞„ÅÆÂàùÊúüÂåñ
    useEffect(() => {
        const q = new Audio("https://www.myinstants.com/media/sounds/nice-shot-wii-sports_DJJ0VOz.mp3");
        const w = new Audio("https://www.myinstants.com/media/sounds/crowdaw.mp3");
        const e = new Audio("https://www.myinstants.com/media/sounds/crowdoh.mp3");
        q.preload = "auto";
        w.preload = "auto";
        e.preload = "auto";
        soundboardRef.current = { q, w, e };
        console.log("[Audio] Audio elements initialized");

        // „É¶„Éº„Ç∂„Éº„Ç§„É≥„Çø„É©„ÇØ„Ç∑„Éß„É≥ÊôÇ„Å´„Ç™„Éº„Éá„Ç£„Ç™„Ç≥„É≥„ÉÜ„Ç≠„Çπ„Éà„Çí„Ç¢„É≥„É≠„ÉÉ„ÇØ
        const unlockOnInteraction = () => {
            if (audioUnlockedRef.current) return;
            console.log("[Audio] User interaction detected, unlocking audio");
            void tryUnlockAudio();
        };

        // „ÇØ„É™„ÉÉ„ÇØ„ÇÑ„Çø„ÉÉ„ÉÅ„Åß„Ç¢„É≥„É≠„ÉÉ„ÇØ„ÇíË©¶Ë°å
        window.addEventListener("click", unlockOnInteraction);
        window.addEventListener("touchstart", unlockOnInteraction);
        window.addEventListener("keydown", unlockOnInteraction);

        return () => {
            window.removeEventListener("click", unlockOnInteraction);
            window.removeEventListener("touchstart", unlockOnInteraction);
            window.removeEventListener("keydown", unlockOnInteraction);
            for (const a of [q, w, e]) {
                try {
                    a.pause();
                } catch {
                    // ignore
                }
            }
            soundboardRef.current = {};
        };
    }, [tryUnlockAudio]);

    // „Çπ„Éö„Éº„Çπ„Ç≠„Éº„Åß„Éá„Éê„ÉÉ„Ç∞„Éë„Éç„É´„Å®IR„Çª„É≥„Çµ„Éº„Éá„Éê„ÉÉ„Ç∞Ë°®Á§∫„ÇíÂàá„ÇäÊõø„Åà
    useEffect(() => {
        const handleKeyDown = (e: KeyboardEvent) => {
            if (e.code === "Space" && e.target === document.body) {
                e.preventDefault();
                setShowDebugPanel(prev => !prev);
                setShowIrDebug(prev => !prev);
            }
        };

        window.addEventListener("keydown", handleKeyDown);
        return () => window.removeEventListener("keydown", handleKeyDown);
    }, []);

    const getOrLoadPdfDocument = useCallback(async (assetId: string) => {
        const cached = pdfDocCacheRef.current.get(assetId);
        if (cached) return await cached;

        const promise = (async () => {
            const blob = await getAssetBlob(assetId);
            if (!blob) throw new Error("PDF„Ç¢„Çª„ÉÉ„Éà„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì (IndexedDB)");
            const arrayBuffer = await blob.arrayBuffer();
            const pdfjs = await import("pdfjs-dist/legacy/build/pdf.mjs");
            pdfjs.GlobalWorkerOptions.workerSrc = new URL(
                "pdfjs-dist/legacy/build/pdf.worker.min.mjs",
                import.meta.url,
            ).toString();
            return await pdfjs.getDocument({ data: arrayBuffer }).promise;
        })();

        pdfDocCacheRef.current.set(assetId, promise);
        try {
            return await promise;
        } catch (e) {
            pdfDocCacheRef.current.delete(assetId);
            throw e;
        }
    }, []);

    // „ÅäÁµµÊèè„ÅçÁî®„ÅÆÂ∫ßÊ®ô„É™„Çπ„Éà
    const [drawingPoints, setDrawingPoints] = useState<Array<{ x: number; y: number; mode?: "draw" | "erase" } | null>>([]);

    // Ê∂à„Åó„Ç¥„É†„É¢„Éº„ÉâÔºà„Éà„Ç∞„É´ÂºèÔºâ- X„Ç≠„Éº„Å®Wii„Éú„Çø„É≥„ÅßÂÖ±ÈÄö
    const [eraserMode, setEraserMode] = useState(false);
    const [eraserButtonName, setEraserButtonName] = useState<string | null>(null);

    // „Ç´„Éº„ÇΩ„É´‰ΩçÁΩÆÔºàÊ∂à„Åó„Ç¥„É†„Ç´„Éº„ÇΩ„É´Ë°®Á§∫Áî®Ôºâ
    const [cursorPos, setCursorPos] = useState<{ x: number; y: number } | null>(null);

    // ÈÄ£Á∂öÈÅ∑Áßª„ÇíÈò≤„Åê„Åü„ÇÅ„ÅÆ„ÇØ„Éº„É´„Çø„Ç§„É†ÁÆ°ÁêÜ
    const lastNavTime = useRef<number>(0);

    // ÁèæÂú®„ÅÆ„Éé„Éº„Éâ„Éá„Éº„Çø
    const currentNode = useMemo(() =>
        flow?.nodes.find((n) => n.id === currentNodeId),
        [flow, currentNodeId]);

    // ‚òÖ‰øÆÊ≠£: ÂàùÂõû„Éû„Ç¶„É≥„ÉàÊôÇ„Å´Ëá™ÂãïÁöÑ„Å´„Éó„É¨„Çº„É≥„ÉÜ„Éº„Ç∑„Éß„É≥„ÇíÈñãÂßã
    useEffect(() => {
        const loaded = loadFromLocalStorage();
        if (!loaded || loaded.nodes.length === 0) {
            setError("„Éá„Éº„Çø„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì„ÄÇEditor„Åß‰ΩúÊàê„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ");
            setFlow(null);
            setCurrentNodeId(null);
            return;
        }

        // „Éê„Ç§„É≥„ÉâË®≠ÂÆö„ÇíË™≠„ÅøËæº„Åø
        const storedBindings = getProjectBindings();
        const flowWithBindings = storedBindings ?
            { ...loaded, projectBindings: storedBindings } : loaded;

        console.log("PresenterView: Auto-starting presentation with bindings", { storedBindings, flowWithBindings });

        setError(null);
        setFlow(flowWithBindings);

        // Start„Éé„Éº„Éâ„Åã„ÇâÈñãÂßã
        const startNode = loaded.nodes.find((n) => n.data.label === "Start") || loaded.nodes[0];
        setCurrentNodeId(startNode.id);

        // WiiÊé•Á∂öÁä∂ÊÖã„ÇíË®òÈå≤
        setStartedWithWii(!!wiiConnected);
        setPlayingSince(Date.now());
    }, []); // ‚òÖÁ©∫„ÅÆ‰æùÂ≠òÈÖçÂàó„ÅßÂàùÂõû„ÅÆ„ÅøÂÆüË°å

    // ‚òÖ‰øÆÊ≠£: wiiConnected„ÅåÂ§âÂåñ„Åó„Åü„ÇâË®òÈå≤„ÇíÊõ¥Êñ∞
    useEffect(() => {
        if (wiiConnected) setStartedWithWii(true);
    }, [wiiConnected]);

    const outgoingEdges = useMemo(() => {
        if (!flow || !currentNodeId) return [];
        return flow.edges.filter((e) => e.source === currentNodeId);
    }, [flow, currentNodeId]);

    const branchOptions = useMemo(() => {
        // 1-9 „ÅÆÊï∞Â≠ó„ÅßÈÅ∏„Åπ„ÇãÂàÜÂ≤ê
        const options: Array<{ key: string; target: string }> = [];
        const used = new Set<string>();

        for (const edge of outgoingEdges) {
            const label = (edge.label ?? "").trim();
            const m = label.match(/^([1-9])(?:\b|\s|:|-)/);
            if (m) {
                const k = m[1];
                if (!used.has(k)) {
                    options.push({ key: k, target: edge.target });
                    used.add(k);
                }
            }
        }

        // „É©„Éô„É´„Å´Áï™Âè∑„Åå„Å™„ÅÑÂ†¥Âêà„ÅØ„ÄÅÈÖçÂàóÈ†Ü„Åß 1..n „ÇíÂâ≤„ÇäÂΩì„Å¶
        for (const edge of outgoingEdges) {
            if (options.length >= 9) break;
            const nextKey = String(options.length + 1);
            if (used.has(nextKey)) continue;
            options.push({ key: nextKey, target: edge.target });
            used.add(nextKey);
        }

        return options;
    }, [outgoingEdges]);

    const hasMultipleBranches = outgoingEdges.length >= 2;

    // „Éé„Éº„ÉâÁßªÂãïÂá¶ÁêÜ
    const navigateTo = useCallback((nodeId: string) => {
        // „ÇØ„Éº„É´„Çø„Ç§„É†„ÉÅ„Çß„ÉÉ„ÇØ (500ms‰ª•ÂÜÖ„ÅÆÈÄ£Á∂öÈÅ∑Áßª„ÅØÁÑ°Ë¶ñ)
        const now = Date.now();
        if (now - lastNavTime.current < 500) return;
        lastNavTime.current = now;

        setCurrentNodeId(nodeId);
        setDrawingPoints([]); // „Çπ„É©„Ç§„Éâ„ÅåÂ§â„Çè„Å£„Åü„ÇâÁ∑ö„ÇíÊ∂à„Åô
    }, []);

    // Ê¨°„Å∏Ôºà„É≠„Ç∏„ÉÉ„ÇØÊîπËâØÁâàÔºâ
    const nextSlide = useCallback(() => {
        if (!flow || !currentNodeId) return;
        // ÂàÜÂ≤ê„ÅåË§áÊï∞„ÅÇ„ÇãÂ†¥Âêà„ÅØ„ÄÅÊï∞Â≠óÈÅ∏Êäû„ÇíÂÑ™ÂÖà„Åô„Çã
        const edges = flow.edges.filter((e) => e.source === currentNodeId);
        if (edges.length >= 2) return;

        // ÁèæÂú®„ÅÆ„Éé„Éº„Éâ„Åã„ÇâÂá∫„Å¶„ÅÑ„Çã„Ç®„ÉÉ„Ç∏„Çí„Åô„Åπ„Å¶ÂèñÂæó
        // (‰∏ä„ÅßÂèñÂæóÊ∏à„Åø)

        if (edges.length === 0) return;

        // ÂÑ™ÂÖàÈ†Ü‰Ωç‰ªò„Åë
        // 1. „É©„Éô„É´„Åå„Å™„ÅÑ„Ç®„ÉÉ„Ç∏ („Éá„Éï„Ç©„É´„Éà„É´„Éº„Éà)
        // 2. „É©„Éô„É´„Åå "next" „ÅÆ„Ç®„ÉÉ„Ç∏
        // 3. „Åù„Çå‰ª•Â§ñ (ÊúÄÂàù„Å´Ë¶ã„Å§„Åã„Å£„Åü„ÇÇ„ÅÆ)
        const targetEdge =
            edges.find(e => !e.label || e.label.trim() === "") ||
            edges.find(e => e.label === "next") ||
            edges[0];

        if (targetEdge) navigateTo(targetEdge.target);
    }, [flow, currentNodeId, navigateTo]);

    const branchByNumberKey = useCallback(
        (key: string) => {
            if (!hasMultipleBranches) return;
            const opt = branchOptions.find((o) => o.key === key);
            if (opt) navigateTo(opt.target);
        },
        [branchOptions, hasMultipleBranches, navigateTo],
    );

    // Ââç„Å∏ÔºàÈÄÜÈ†ÜÊ§úÁ¥¢Ôºâ
    const prevSlide = useCallback(() => {
        if (!flow || !currentNodeId) return;
        // Ëá™ÂàÜ„Å´Âêë„Åã„Å£„Å¶„ÅÑ„Çã„Ç®„ÉÉ„Ç∏„ÇíÊé¢„Åó„Å¶Êàª„ÇãÔºàÁ∞°ÊòìÂÆüË£ÖÔºâ
        const edge = flow.edges.find(e => e.target === currentNodeId);
        if (edge) navigateTo(edge.source);
    }, [flow, currentNodeId, navigateTo]);

    // ÂàÜÂ≤êÂá¶ÁêÜÔºà„Ç®„ÉÉ„Ç∏„ÅÆ„É©„Éô„É´„ÅßÊ§úÁ¥¢Ôºâ
    const branchTo = useCallback((keywords: string[]) => {
        if (!flow || !currentNodeId) return;
        const edges = flow.edges.filter(e => e.source === currentNodeId);
        const target = edges.find(e => keywords.some(k => e.label?.includes(k)));
        if (target) {
            console.log("ÂàÜÂ≤ê„Åó„Åæ„Åó„Åü:", target.label);
            navigateTo(target.target);
        }
    }, [flow, currentNodeId, navigateTo]);

    // ‚òÖËøΩÂä†: „É™„Ç¢„ÇØ„Ç∑„Éß„É≥„Çí„Éá„Éê„ÉÉ„Ç∞„Åô„ÇãÔºàN=One, M=TwoÔºâ
    const [debugEmitClap, setDebugEmitClap] = useState(false);
    const [debugEmitLaugh, setDebugEmitLaugh] = useState(false);

    // „Ç≠„Éº„Éú„Éº„ÉâÊìç‰Ωú (Áü¢Âç∞„Ç≠„ÉºÂØæÂøú + ESC„ÅßÊàª„Çã)
    useEffect(() => {
        if (!isPlaying) return;

        const handleKeyDown = (e: KeyboardEvent) => {
            // ËøΩÂä†: Á∑ö„Çí„ÇØ„É™„Ç¢ (R)
            if (e.key === "r" || e.key === "R") {
                setDrawingPoints([]);
                isMouseDrawingRef.current = false;
                wasWiiADownRef.current = false;
                return;
            }

            // „Éö„Ç§„É≥„Éà„Å®Ê∂à„Åó„Ç¥„É†„ÇíÂàá„ÇäÊõø„Åà (X) - Wii„Éú„Çø„É≥„Å®Âêå„ÅòÊåôÂãï
            if (e.key === "x" || e.key === "X") {
                // Êäº„Åó„Å£„Å±„Å™„Åó„ÅßON/OFF„ÅåÊö¥„Çå„Å™„ÅÑ„Çà„ÅÜ„Å´„ÄÅ„É™„Éî„Éº„Éà„ÅØÁÑ°Ë¶ñ
                if (e.repeat) return;
                setEraserMode((prev) => {
                    const next = !prev;
                    if (next) {
                        setEraserButtonName("X");
                        setCursorPos({ x: window.innerWidth / 2, y: window.innerHeight / 2 });
                    } else {
                        setEraserButtonName(null);
                        setCursorPos(null);
                    }
                    return next;
                });
                return;
            }

            if (!e.repeat) {
                if (e.key === "q" || e.key === "Q") {
                    playSound("q");
                    return;
                }
                if (e.key === "w" || e.key === "W") {
                    playSound("w");
                    return;
                }
                if (e.key === "e" || e.key === "E") {
                    playSound("e");
                    return;
                }
            }

            // ‚òÖËøΩÂä†: „É™„Ç¢„ÇØ„Ç∑„Éß„É≥ÔºàN / MÔºâ
            // Êäº„Åó„Å£„Å±„Å™„Åó„ÅßÂ¢óÊÆñ„Åó„Å™„ÅÑ„Çà„ÅÜ„Å´ repeat „ÇíÁÑ°Ë¶ñ
            if (!e.repeat) {
                if (e.key === "n" || e.key === "N") {
                    setDebugEmitClap(true);
                    queueMicrotask(() => setDebugEmitClap(false)); // 1Âõû„Å†„ÅëÁô∫ÁÅ´
                    return;
                }
                if (e.key === "m" || e.key === "M") {
                    setDebugEmitLaugh(true);
                    queueMicrotask(() => setDebugEmitLaugh(false)); // 1Âõû„Å†„ÅëÁô∫ÁÅ´
                    return;
                }
            }

            // Êó¢Â≠ò: ÂàÜÂ≤ê 1..9
            if (e.key >= "1" && e.key <= "9") {
                branchByNumberKey(e.key);
                return;
            }
            if (e.key === "ArrowRight") {
                if (!hasMultipleBranches) nextSlide();
            }
            if (e.key === "ArrowLeft") prevSlide();
            // ESC„Ç≠„Éº„ÅßÂÖÉ„ÅÆÁîªÈù¢„Å∏Êàª„ÇãÔºà„Ç®„Éá„Ç£„ÇøÁµåÁî±„Å™„Çâ„Ç®„Éá„Ç£„Çø„Å∏Ôºâ
            if (e.key === "Escape") goBack();

            // ‚òÖËøΩÂä†: C„Ç≠„Éº„ÅßIR„Ç´„Éº„ÇΩ„É´ÂàáÊõø
            if ((e.key === "c" || e.key === "C") && !e.repeat) {
                setIrCursorEnabled(!irCursorEnabled);
                return;
            }
        };

        window.addEventListener("keydown", handleKeyDown);
        return () => window.removeEventListener("keydown", handleKeyDown);
    }, [isPlaying, nextSlide, prevSlide, goBack, branchByNumberKey, hasMultipleBranches, playSound, irCursorEnabled, setIrCursorEnabled]);

    const effectiveProjectBindings = useMemo(() => {
        // „Éó„É≠„Ç∏„Çß„ÇØ„ÉàÂÖ®‰Ωì„ÅÆÂâ≤ÂΩì + „Çπ„É©„Ç§„ÉâÂà•„ÅÆÂâ≤ÂΩìÔºà„ÅÇ„Çå„Å∞‰∏äÊõ∏„ÅçÔºâ„ÇíÂêàÊàê
        const combined = {
            ...(flow?.projectBindings ?? {}),
            ...(currentNode?.data.bindings ?? {}),
        };
        const merged = mergeBindings(combined);
        console.log("PresenterView: effectiveProjectBindings updated", {
            flowBindings: flow?.projectBindings,
            slideBindings: currentNode?.data.bindings,
            merged,
        });
        return merged;
    }, [flow, currentNode]);

    // --- „Éó„É≠„Ç∏„Çß„ÇØ„ÉàÂÖ®‰Ωì„Éê„Ç§„É≥„Éâ„ÇíÈÅ©Áî®„Åó„Å¶„Ç¢„ÇØ„Ç∑„Éß„É≥ÂÆüË°å ---
    const runAction = useCallback(
        (act: BindingAction, btnName?: string) => {
            // Ê∂à„Åó„Ç¥„É†„É¢„Éº„Éâ‰∏≠„ÅØ„ÄÅeraser„Ç¢„ÇØ„Ç∑„Éß„É≥‰ª•Â§ñ„ÇíÁÑ°ÂäπÂåñ
            if (eraserMode && act.type !== "eraser") {
                return;
            }

            // ‚òÖ„Éá„Éê„ÉÉ„Ç∞: „Ç¢„ÇØ„Ç∑„Éß„É≥ÂÆüË°å„Çí„É≠„Ç∞Âá∫Âäõ
            console.log(`[WiiAction] Button: ${btnName || "unknown"}, Action:`, act);

            switch (act.type) {
                case "next":
                    nextSlide();
                    return;
                case "prev":
                    prevSlide();
                    return;
                case "branchIndex":
                    // 1..9 „Çí ‚ÄúÂàÜÂ≤êÈÅ∏ÊäûÔºàÊï∞Â≠ó„Ç≠„ÉºÔºâ‚Äù „Å®Âêå„ÅòÊåôÂãï„Å´„Åô„Çã
                    branchByNumberKey(String(act.index));
                    return;
                case "branch": {
                    // Êó¢Â≠ò‰∫íÊèõ: A/B/HOME „ÅØ 1..3 „Å´„Éû„ÉÉ„Éó
                    if (!hasMultipleBranches) return;
                    const map: Record<string, string> = { A: "1", B: "2", HOME: "3" };
                    const k = map[act.kind];
                    if (k) branchByNumberKey(k);
                    return;
                }
                case "reaction":
                    // ReactionOverlay „Åå pressed.One/Two „ÇíË¶ã„Å¶„ÅÑ„Çã„ÅÆ„Åß„ÄÅ„Åì„Åì„Åß„ÅØ‰Ωï„ÇÇ„Åó„Å™„ÅÑ
                    return;
                case "paint":
                    // shouldPaint„ÅßÂà•ÈÄîÂá¶ÁêÜ„Åô„Çã„ÅÆ„Åß„ÄÅ„Åì„Åì„Åß„ÅØ‰Ωï„ÇÇ„Åó„Å™„ÅÑ
                    break;
                case "eraser":
                    // „Éà„Ç∞„É´Âºè„Å´Âàá„ÇäÊõø„ÅàÔºà500ms„ÇØ„Éº„É´„Çø„Ç§„É†„ÅßÂ§öÈáçÂÖ•ÂäõÈò≤Ê≠¢Ôºâ
                    const nowEraser = Date.now();
                    if (nowEraser - lastEraserToggleTimeRef.current < 500) {
                        console.log("[Eraser] Ignoring rapid toggle");
                        return;
                    }
                    lastEraserToggleTimeRef.current = nowEraser;

                    if (eraserMode) {
                        // Ëß£Èô§
                        setEraserMode(false);
                        setEraserButtonName(null);
                        setCursorPos(null);
                    } else {
                        // ON
                        setEraserMode(true);
                        setEraserButtonName(btnName || "unknown");
                        // „Ç´„Éº„ÇΩ„É´„ÇíÁîªÈù¢‰∏≠Â§Æ„Å´
                        setCursorPos({ x: window.innerWidth / 2, y: window.innerHeight / 2 });
                    }
                    break;
                case "sound":
                    // Èü≥Â£∞ÂÜçÁîüÂá¶ÁêÜÔºàoutputDevice„Å´Âøú„Åò„Å¶PC„Åæ„Åü„ÅØWii„ÅßÂÜçÁîüÔºâ
                    if (act.kind === "shot") playSound("q", act.outputDevice);
                    else if (act.kind === "oh") playSound("e", act.outputDevice);
                    else if (act.kind === "uxo") playSound("w", act.outputDevice);
                    return;
                case "remove":
                    // ÊèèÁîª„ÇíÊ∂àÂéª
                    setDrawingPoints([]);
                    isMouseDrawingRef.current = false;
                    wasWiiADownRef.current = false;
                    return;
                case "irSens":
                    // IR„Çª„É≥„Çµ„Éº„Ç´„Éº„ÇΩ„É´„ÅÆÂàáÊõøÔºà500ms„ÇØ„Éº„É´„Çø„Ç§„É†„ÅßÂ§öÈáçÂÖ•ÂäõÈò≤Ê≠¢Ôºâ
                    const nowIrSens = Date.now();
                    if (nowIrSens - lastIrSensToggleTimeRef.current < 500) {
                        console.log("[IRSens] Ignoring rapid toggle");
                        return;
                    }
                    lastIrSensToggleTimeRef.current = nowIrSens;

                    setIrCursorEnabled(!irCursorEnabled);
                    return;
                case "none":
                default:
                    return;
            }
        },
        [nextSlide, prevSlide, branchByNumberKey, hasMultipleBranches, playSound, eraserMode, irCursorEnabled, setIrCursorEnabled],
    );

    // ‚òÖ‰øÆÊ≠£: Wii„É™„É¢„Ç≥„É≥„ÅÆ„Éú„Çø„É≥Âá¶ÁêÜÔºàisPlaying„Ååtrue„ÅÆÊôÇ„ÅÆ„ÅøÂãï‰ΩúÔºâ
    // pressed „ÅØ„Äå„Åì„ÅÆ„Éï„É¨„Éº„É†„ÅßÊäº„Åï„Çå„ÅüÁû¨Èñì„Äç„ÅÆ„Éú„Çø„É≥„ÅÆ„ÅøÂê´„ÇÄÔºàuseWiiControllerÂÅ¥„ÅßÂá¶ÁêÜÊ∏à„ÅøÔºâ
    useEffect(() => {
        if (!isPlaying) return;

        // pressed„Å´Âê´„Åæ„Çå„Çã„Éú„Çø„É≥„ÇíÂÖ®„Å¶Âá¶ÁêÜÔºàÊó¢„Å´„ÄåÊäº„Åï„Çå„ÅüÁû¨Èñì„Äç„ÅÆ„ÅøÊäΩÂá∫Ê∏à„ÅøÔºâ
        for (const btn of Object.keys(pressed)) {
            const isDown = (pressed as Record<string, boolean>)[btn];
            if (!isDown) continue;

            // ‚òÖ„Éá„Éê„ÉÉ„Ç∞: „Éú„Çø„É≥Êäº‰∏ãÊ§úÂá∫„Çí„É≠„Ç∞Âá∫Âäõ
            console.log(`[WiiPress] Button pressed: ${btn}`);

            const act = (effectiveProjectBindings as Record<string, BindingAction | undefined>)[btn] ?? { type: "none" };

            // paint/eraser‰ª•Â§ñ„ÅÆ„Ç¢„ÇØ„Ç∑„Éß„É≥„ÇíÂÆüË°å
            if (act.type !== "paint" && act.type !== "eraser") {
                runAction(act, btn);
            } else if (act.type === "eraser") {
                // eraser„Éú„Çø„É≥„ÅØÂ∏∏„Å´„Éà„Ç∞„É´ÂèØËÉΩ
                runAction(act, btn);
            }
        }
    }, [pressed, isPlaying, effectiveProjectBindings, runAction]);

    // ‚òÖËøΩÂä†: „É™„Ç¢„ÇØ„Ç∑„Éß„É≥Ê§úÂá∫Ôºà„Éê„Ç§„É≥„Éâ„Éô„Éº„ÇπÔºâ
    const shouldEmitClap = useMemo(() => {
        if (!isPlaying) return false;
        // Êäº„Åï„Çå„Åü„Éú„Çø„É≥„ÅÆ‰∏≠„Åß„ÄÅ"clap" „Å´„Éê„Ç§„É≥„Éâ„Åï„Çå„Å¶„ÅÑ„Çã„ÇÇ„ÅÆ„Åå„ÅÇ„Çã„ÅãÔºü
        for (const btn of Object.keys(pressed)) {
            const isDown = (pressed as Record<string, boolean>)[btn];
            if (!isDown) continue;
            const act = (effectiveProjectBindings as Record<string, BindingAction | undefined>)[btn];
            if (act?.type === "reaction" && act.kind === "clap") return true;
        }
        return false;
    }, [pressed, effectiveProjectBindings, isPlaying]);

    const shouldEmitLaugh = useMemo(() => {
        if (!isPlaying) return false;
        for (const btn of Object.keys(pressed)) {
            const isDown = (pressed as Record<string, boolean>)[btn];
            if (!isDown) continue;
            const act = (effectiveProjectBindings as Record<string, BindingAction | undefined>)[btn];
            if (act?.type === "reaction" && act.kind === "laugh") return true;
        }
        return false;
    }, [pressed, effectiveProjectBindings, isPlaying]);

    // PAINT„Éú„Çø„É≥„ÅÆÊúÄÂæå„ÅÆÂÖ•ÂäõÊôÇÂàª„ÇíË®òÈå≤
    const lastPaintInputTimeRef = useRef<number>(0);
    const [shouldPaint, setShouldPaint] = useState(false);

    // ‚òÖËøΩÂä†: IR Cursor „ÇíOFF„Å´„Åó„ÅüÁû¨Èñì„Å´„ÄåWii PAINTÊèèÁîª„Äç„ÇíÂÆåÂÖ®ÂÅúÊ≠¢ÔºàÊÆãÁïô shouldPaint „ÇíÊΩ∞„ÅôÔºâ
    useEffect(() => {
        if (irCursorEnabled) return;

        // Wii„Éö„Ç§„É≥„ÉàÁ≥ª„ÅÆÁä∂ÊÖã„ÇíÂº∑Âà∂„É™„Çª„ÉÉ„Éà
        setShouldPaint(false);
        setIsPainting(false);
        lastPaintInputTimeRef.current = 0;

        // „ÇÇ„ÅóWiiÊèèÁîª„ÅÆÁ∑ö„ÅåÁ∂ôÁ∂ö‰∏≠„Å™„ÇâÂå∫Âàá„Çã
        wasWiiADownRef.current = false;
        setDrawingPoints((prev) =>
            prev.length > 0 && prev[prev.length - 1] !== null ? [...prev, null] : prev
        );
    }, [irCursorEnabled]);

    // wiiState.buttons„Çí„ÉÅ„Çß„ÉÉ„ÇØ„Åó„Å¶„ÄÅÁèæÂú®PAINT„Éú„Çø„É≥„ÅåÊäº„Åï„Çå„Å¶„ÅÑ„Çã„ÅãÁ∂ôÁ∂öÁöÑ„Å´Áõ£Ë¶ñ
    useEffect(() => {
        // ‚òÖÈáçË¶Å: IR Cursor „ÅåOFF„Å™„Çâ Wii„Éö„Ç§„É≥„Éà„ÅØÁµ∂ÂØæ„Å´Âãï„Åã„Åï„Å™„ÅÑ
        if (!isPlaying || !wiiState || eraserMode || !irCursorEnabled) {
            if (shouldPaint) {
                setShouldPaint(false);
                setIsPainting(false);
            }
            return;
        }

        // ÁèæÂú®Êäº„Åï„Çå„Å¶„ÅÑ„Çã„Éú„Çø„É≥„ÅÆ‰∏≠„Å´PAINT„Åå„ÅÇ„Çã„Åã„ÉÅ„Çß„ÉÉ„ÇØ
        let isPaintButtonPressed = false;
        for (const btn of Object.keys(wiiState.buttons)) {
            const isDown = (wiiState.buttons as Record<string, boolean>)[btn];
            if (!isDown) continue;

            const act = (effectiveProjectBindings as Record<string, BindingAction | undefined>)[btn];
            if (act?.type === "paint") {
                isPaintButtonPressed = true;
                break;
            }
        }

        if (isPaintButtonPressed) {
            lastPaintInputTimeRef.current = Date.now();
            if (!shouldPaint) setShouldPaint(true);
            if (!isPainting) setIsPainting(true); // ‚òÖIRÊèèÁîª‰∏≠„ÇÇ„Éö„É≥„Ç´„Éº„ÇΩ„É´Ë°®Á§∫
        }
    }, [wiiState, effectiveProjectBindings, isPlaying, eraserMode, irCursorEnabled, shouldPaint, isPainting]);

    // 200ms„Çø„Ç§„Éû„Éº„ÅßÊèèÁîªÁä∂ÊÖã„Çí„ÉÅ„Çß„ÉÉ„ÇØ
    useEffect(() => {
        if (!isPlaying) {
            setShouldPaint(false);
            return;
        }

        const interval = setInterval(() => {
            const now = Date.now();
            const paintElapsed = now - lastPaintInputTimeRef.current;

            if (paintElapsed > 100 && shouldPaint) {
                setShouldPaint(false);
                setIsPainting(false); // ‚òÖËøΩÂä†: Wii„Éú„Çø„É≥ÊèèÁîªÁµÇ‰∫ÜÊôÇ„ÇÇ„Éö„É≥„Ç´„Éº„ÇΩ„É´Ëß£Èô§
                // ÊèèÁîª„ÇíÁµÇ‰∫Ü
                if (isMouseDrawingRef.current) {
                    isMouseDrawingRef.current = false;
                    setDrawingPoints((prev) => (prev.length > 0 && prev[prev.length - 1] !== null ? [...prev, null] : prev));
                }
            }
        }, 50); // 50ms„Åî„Å®„Å´„ÉÅ„Çß„ÉÉ„ÇØ

        return () => clearInterval(interval);
    }, [isPlaying, shouldPaint]);

    // --- ÊèèÁîª/Ê∂à„Åó„Ç¥„É†„É≠„Ç∏„ÉÉ„ÇØ (IR„Çª„É≥„Çµ„Éº & PAINT„Éú„Çø„É≥) ---
    useEffect(() => {
        // IR„Ç´„Éº„ÇΩ„É´OFF„ÅÆÂ†¥Âêà„ÅØIRÊèèÁîª„ÇíÁÑ°ÂäπÂåñ
        if (!irCursorEnabled) return;

        if (!wiiState || !wiiState.cursor) return;

        // ‚òÖIR„Ç´„Éº„ÇΩ„É´ONÊôÇÔºö„Éê„ÉÉ„ÇØ„Ç®„É≥„Éâ„Åã„ÇâÂèó‰ø°„Åó„ÅüÊ≠£Ë¶èÂåñÊ∏à„ÅøcursorÂ∫ßÊ®ô„ÅÆ„Åø„Çí‰ΩøÁî®
        // ‚òÖÈáçË¶ÅÔºöCSS„Åßcursor: url("/pen.png") 0 0„Çí‰ΩøÁî®„Åó„Å¶„ÅÑ„Çã„Åü„ÇÅ„ÄÅ
        // SetCursorPos„ÅØ„Éö„É≥ÁîªÂÉè„ÅÆÂ∑¶‰∏äÔºà0,0Ôºâ„ÇíÂãï„Åã„Åô„ÄÇ
        // „Åó„Åã„ÅóÊèèÁîª„ÅØ„Éö„É≥ÂÖà„ÅÆ‰ΩçÁΩÆÔºà‰æã: 6, 28Ôºâ„ÅßË°å„ÅÜÂøÖË¶Å„Åå„ÅÇ„Çã„Åü„ÇÅ„ÄÅ„Ç™„Éï„Çª„ÉÉ„Éà„ÇíÂä†ÁÆó„ÄÇ
        const PEN_TIP_OFFSET_X = 0;  // pen.png„ÅÆ„Éö„É≥ÂÖàXÂ∫ßÊ®ôÔºàÂÆüÈöõ„ÅÆÁîªÂÉè„Å´Âêà„Çè„Åõ„Å¶Ë™øÊï¥Ôºâ
        const PEN_TIP_OFFSET_Y = 0; // pen.png„ÅÆ„Éö„É≥ÂÖàYÂ∫ßÊ®ôÔºàÂÆüÈöõ„ÅÆÁîªÂÉè„Å´Âêà„Çè„Åõ„Å¶Ë™øÊï¥Ôºâ

        const pos = {
            x: wiiState.cursor.x * window.innerWidth + PEN_TIP_OFFSET_X,
            y: wiiState.cursor.y * window.innerHeight + PEN_TIP_OFFSET_Y,
        };

        // Ê∂à„Åó„Ç¥„É†„É¢„Éº„Éâ‰∏≠: IR„Åß„Ç´„Éº„ÇΩ„É´„ÇíÁßªÂãï
        if (eraserMode) {
            setCursorPos(pos);

            // A„Å®B„ÇíÂêåÊôÇÊäº„Åó„Åó„Å¶„ÅÑ„Çã„Åã„ÉÅ„Çß„ÉÉ„ÇØ
            const isAPressed = wiiState.buttons.A;
            const isBPressed = wiiState.buttons.B;

            if (isAPressed && isBPressed) {
                // A+BÂêåÊôÇÊäº„Åó„ÅßÊ∂àÂéª
                setDrawingPoints((prev) => {
                    const next = prev.slice();
                    if (!wasWiiADownRef.current) {
                        if (next.length > 0 && next[next.length - 1] !== null) next.push(null);
                    }
                    next.push({ ...pos, mode: "erase" });
                    return next;
                });
                wasWiiADownRef.current = true;
            } else {
                // A+B„ÇíÈõ¢„Åó„Åü„ÇâÂå∫Âàá„Çã
                if (wasWiiADownRef.current) {
                    wasWiiADownRef.current = false;
                    setDrawingPoints((prev) => (prev.length > 0 && prev[prev.length - 1] !== null ? [...prev, null] : prev));
                }
            }
            return;
        }

        // PAINT„Éê„Ç§„É≥„Éâ„Åï„Çå„Åü„Éú„Çø„É≥„ÇíÊäº„Åó„Å¶„ÅÑ„ÇãÈñì„ÄÅËªåË∑°„ÇíËøΩÂä†
        if (shouldPaint) {
            setDrawingPoints((prev) => {
                const next = prev.slice();
                if (!wasWiiADownRef.current) {
                    // ÂâçÂõû„ÅÆÁ∑ö„Å®Áπã„Åå„Çâ„Å™„ÅÑ„Çà„ÅÜ„Å´Âå∫Âàá„Çä„ÇíÂÖ•„Çå„Çã
                    if (next.length > 0 && next[next.length - 1] !== null) next.push(null);
                }
                next.push({ ...pos, mode: "draw" });
                return next;
            });
            wasWiiADownRef.current = true;
        } else {
            // Èõ¢„Åó„Åü„Çø„Ç§„Éü„É≥„Ç∞„ÅßÂå∫Âàá„Çã
            if (wasWiiADownRef.current) {
                wasWiiADownRef.current = false;
                setDrawingPoints((prev) => (prev.length > 0 && prev[prev.length - 1] !== null ? [...prev, null] : prev));
            }
        }
    }, [wiiState, shouldPaint, irCursorEnabled]);


    return (
        <main
            ref={containerRef}
            onMouseDown={(e) => {
                if (!isPlaying) return;
                if (e.button !== 0) return;
                // UI(„Éú„Çø„É≥Á≠â)Êìç‰Ωú„ÅØÈÇ™È≠î„Åó„Å™„ÅÑ
                const el = e.target as HTMLElement | null;
                if (el && el.closest("button, a, input, textarea, select")) return;
                e.preventDefault();

                // Ê∂à„Åó„Ç¥„É†„É¢„Éº„Éâ‰∏≠„ÅØÂ∑¶„ÇØ„É™„ÉÉ„ÇØ„ÅßÊ∂àÂéªÈñãÂßã
                if (eraserMode) {
                    isMouseDrawingRef.current = true;
                    setIsPainting(false); // Ê∂à„Åó„Ç¥„É†„É¢„Éº„Éâ‰∏≠„ÅØ„Éö„É≥„Ç´„Éº„ÇΩ„É´„Å´„Åó„Å™„ÅÑ
                    setDrawingPoints((prev) => {
                        const next = prev.slice();
                        if (next.length > 0 && next[next.length - 1] !== null) next.push(null);
                        next.push({ x: e.clientX, y: e.clientY, mode: "erase" });
                        return next;
                    });
                    return;
                }

                // ÈÄöÂ∏∏„É¢„Éº„ÉâÔºöÂ∑¶„ÇØ„É™„ÉÉ„ÇØ„ÅßÊèèÁîªÈñãÂßã
                isMouseDrawingRef.current = true;
                setIsPainting(true); // „Éö„É≥„Ç´„Éº„ÇΩ„É´„Å´Â§âÊõ¥
                setDrawingPoints((prev) => {
                    const next = prev.slice();
                    if (next.length > 0 && next[next.length - 1] !== null) next.push(null);
                    next.push({ x: e.clientX, y: e.clientY, mode: "draw" });
                    return next;
                });
            }}
            onMouseMove={(e) => {
                if (!isPlaying) return;

                // Ê∂à„Åó„Ç¥„É†„É¢„Éº„ÉâÊôÇÔºö„Ç´„Éº„ÇΩ„É´‰ΩçÁΩÆ„ÇíÂ∏∏„Å´Êõ¥Êñ∞ÔºàËµ§„ÅÑÂÜÜ„ÇíËøΩÂæì„Åï„Åõ„ÇãÔºâ
                if (eraserMode) {
                    setCursorPos({ x: e.clientX, y: e.clientY });

                    // „Éû„Ç¶„Çπ„Éâ„É©„ÉÉ„Ç∞‰∏≠„ÅÆ„ÅøÊ∂àÂéª„ÇíÂÆüË°å
                    if (isMouseDrawingRef.current) {
                        e.preventDefault();
                        setDrawingPoints((prev) => {
                            const last = prev[prev.length - 1];
                            if (last && last.x && Math.abs(last.x - e.clientX) + Math.abs(last.y - e.clientY) < 2) return prev;
                            return [...prev, { x: e.clientX, y: e.clientY, mode: "erase" }];
                        });
                    }
                    return;
                }

                // --- ÈÄöÂ∏∏„É¢„Éº„ÉâÔºöÊèèÁîªÔºà„Éû„Ç¶„Çπ or Wii PAINTÔºâ ---

                // 1) „Éû„Ç¶„Çπ„Éâ„É©„ÉÉ„Ç∞‰∏≠„ÅØÂ∏∏„Å´„Éû„Ç¶„ÇπÊèèÁîª„ÇíÂÑ™ÂÖà
                if (isMouseDrawingRef.current) {
                    e.preventDefault();
                    setDrawingPoints((prev) => {
                        const last = prev[prev.length - 1];
                        if (last && last.x && Math.abs(last.x - e.clientX) + Math.abs(last.y - e.clientY) < 2) return prev;
                        return [...prev, { x: e.clientX, y: e.clientY, mode: "draw" }];
                    });
                    return;
                }

                // 2) Wii„ÅÆPAINTÔºàshouldPaint=trueÔºâ„ÅÆÂ†¥Âêà
                if (shouldPaint) {
                    // ‚òÖÈáçË¶ÅÔºöIR Cursor ON „ÅÆ„Å®„Åç„ÅØ„ÄåIRÂÅ¥useEffect„Äç„ÅåÁÇπ„ÇíËøΩÂä†„Åô„Çã„ÄÇ
                    // „Åì„Åì„Åß„Éû„Ç¶„ÇπÂ∫ßÊ®ô(e.clientX/Y)„ÇíÊ∑∑„Åú„Çã„Å®„ÄÅÂà•Â∫ßÊ®ô„ÅåÊ∑∑„Åñ„Å£„Å¶Á∑ö„Åå‰º∏„Å≥„Çã„ÅÆ„Åß‰Ωï„ÇÇ„Åó„Å™„ÅÑ„ÄÇ
                    if (irCursorEnabled) return;

                    // ‚òÖIR Cursor OFF „ÅÆ„Å®„Åç„Å†„Åë„Äå„Éû„Ç¶„ÇπÂ∫ßÊ®ô„Åß1ÁÇπ„Å†„ÅëÊèè„Åè„ÄçÊåôÂãï„ÇíË®±ÂèØÔºà‰ªïÊßòÈÄö„Çä„ÅÆÊåôÂãïÔºâ
                    e.preventDefault();
                    setDrawingPoints((prev) => {
                        const next = prev.slice();
                        if (next.length > 0 && next[next.length - 1] !== null) next.push(null);
                        next.push({ x: e.clientX, y: e.clientY, mode: "draw" });
                        next.push(null); // ‚îÖ1Êú¨„Å†„Åë„ÅßÂøÖ„ÅöÂå∫Âàá„Çã
                        return next;
                    });
                    return;
                }

                // 3) ‰Ωï„ÇÇ„Åó„Å¶„Å™„ÅÑ„Å™„Çâ‰Ωï„ÇÇ„Åó„Å™„ÅÑ
                return;
            }}
            onMouseUp={() => {
                if (!isMouseDrawingRef.current) return;
                isMouseDrawingRef.current = false;
                setIsPainting(false); // „Éö„É≥„Ç´„Éº„ÇΩ„É´Ëß£Èô§
                setDrawingPoints((prev) => (prev.length > 0 && prev[prev.length - 1] !== null ? [...prev, null] : prev));
            }}
            onMouseLeave={() => {
                if (!isMouseDrawingRef.current) return;
                isMouseDrawingRef.current = false;
                setIsPainting(false); // „Éö„É≥„Ç´„Éº„ÇΩ„É´Ëß£Èô§
                setDrawingPoints((prev) => (prev.length > 0 && prev[prev.length - 1] !== null ? [...prev, null] : prev));
            }}
            className={isPainting ? 'presenter-painting' : 'presenter-container'}
            style={{
                position: "relative",
                width: "100vw",
                height: "100vh",
                overflow: "hidden",
                background: "black",
            }}
        >

            <WiiDisconnectPopup
                isPlaying={isPlaying}
                startedWithWii={startedWithWii}
                wiiConnected={wiiConnected}
                wiiDisconnectedAt={wiiDisconnectedAt}
                playingSince={playingSince}
            />

            <WiiReconnectPopup
                isPlaying={isPlaying}
                wiiConnected={wiiConnected}
                startedWithWii={startedWithWii}
            />

            {/* Êàª„Çã„Éú„Çø„É≥ÔºàÂ∑¶‰∏äÔºâ */}
            <div style={{ position: "absolute", top: 20, left: 20, zIndex: 10000 }}>
                <button onClick={goBack} style={{ padding: "10px 14px", fontSize: 14 }}>
                    {returnLabel}
                </button>
            </div>

            {/* „É™„Ç¢„ÇØ„Ç∑„Éß„É≥ */}
            <ReactionOverlay emitClap={shouldEmitClap} emitLaugh={shouldEmitLaugh} />

            {/* IR„Éù„Ç§„É≥„Çø„Éº„Ç™„Éº„Éê„Éº„É¨„Ç§ */}
            <IrPointerOverlay
                wiiState={wiiState}
                isPlaying={isPlaying}
                irCursorEnabled={irCursorEnabled}
                showIrDebug={showIrDebug}
            />

            {/* „Çπ„É©„Ç§„ÉâË°®Á§∫„Ç®„É™„Ç¢ (ÂÖ®ÁîªÈù¢„Éª‰ΩôÁôΩ„Å™„Åó„Éª„Ç¢„Çπ„Éö„ÇØ„ÉàÊØîÁ∂≠ÊåÅ) */}
            <SlideDisplay
                currentNode={currentNode}
                error={error}
                getOrLoadPdfDocument={getOrLoadPdfDocument}
            />

            {/* ÊèèÁîª„É¨„Ç§„É§„Éº (ÊúÄÂâçÈù¢) */}
            <DrawingCanvas
                drawingPoints={drawingPoints}
                wiiState={wiiState}
                isPlaying={isPlaying}
                shouldPaint={shouldPaint}
                eraserMode={eraserMode}
                eraserPosition={cursorPos}
            />

            {/* „Éá„Éê„ÉÉ„Ç∞ÊÉÖÂ†± (Âè≥‰∏ä) */}
            {showDebugPanel && (
                <WiiDebugPanel
                    wiiState={wiiState}
                    pressed={pressed}
                    effectiveProjectBindings={effectiveProjectBindings}
                    irCursorEnabled={irCursorEnabled}
                    onToggleIrCursor={() => setIrCursorEnabled(!irCursorEnabled)}
                />
            )}

            {/* Êìç‰Ωú„Ç¨„Ç§„Éâ (Â∑¶‰∏ã) */}
            <div style={{ position: "absolute", bottom: 20, left: 20, color: "rgba(255,255,255,0.5)", fontSize: 14, pointerEvents: "none" }}>
                [ESC] Êàª„Çã | [SPACE] „Éá„Éê„ÉÉ„Ç∞Ë°®Á§∫ÂàáÊõø
            </div>

            {/* Ê∂à„Åó„Ç¥„É†„Ç´„Éº„ÇΩ„É´ */}
            <EraserCursor
                position={cursorPos}
                isActive={eraserMode}
                buttonName={eraserButtonName}
            />

            {/* Ê∂à„Åó„Ç¥„É†„Ç´„Éº„ÇΩ„É´ */}
            {eraserMode && cursorPos && (
                <div
                    style={{
                        position: "absolute",
                        left: cursorPos.x,
                        top: cursorPos.y,
                        width: 120,
                        height: 120,
                        borderRadius: "50%",
                        border: "3px dashed rgba(255, 100, 100, 0.8)",
                        background: "rgba(255, 100, 100, 0.2)",
                        transform: "translate(-50%, -50%)",
                        pointerEvents: "none",
                        zIndex: 10000,
                    }}
                />
            )}
        </main>
    );
}
